.footer {
  background-color: #1e2a47; /* Màu nền đậm */
  color: #fff;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  border-radius: 10px; /* Góc bo tròn */
  position: relative;
  overflow: hidden;

  /* Hiệu ứng viền chạy màu */
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 2px solid transparent;
    background: linear-gradient(45deg, #ffcc00, #ff0066, #00ccff, #66ff00);
    background-size: 400% 400%;
    animation: borderAnimation 6s ease infinite;
    border-radius: 10px;
    z-index: -1; /* Đặt phía sau nội dung */
  }

  /* Animation của viền chạy màu */
  @keyframes borderAnimation {
    0% {
      background-position: 400% 0;
    }
    50% {
      background-position: 0 400%;
    }
    100% {
      background-position: 400% 0;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;
    max-width: 1200px;
    margin: 0 auto;
  }

  &__section {
    flex: 1;
    min-width: 250px;
    padding: 20px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;

    &:hover {
      transform: translateY(-10px); /* Di chuyển lên khi hover */
      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2); /* Thêm bóng đổ */
    }

    &-title {
      font-size: 1.25rem;
      margin-bottom: 10px;
      font-weight: bold;
      color: #fff;
    }

    &-links {
      list-style: none;
      padding: 0;

      li {
        margin: 8px 0;

        a {
          color: #ccc;
          text-decoration: none;
          font-size: 1rem;
          transition: color 0.3s ease, transform 0.3s ease;

          &:hover {
            color: #ffcc00; /* Màu vàng khi hover */
            transform: scale(1.1); /* Phóng to khi hover */
          }
        }
      }
    }
  }

  &__icons {
    display: flex;
    gap: 20px;
    justify-content: center;
    transition: transform 0.3s ease;

    .footer__icon-link {
      color: #ccc;
      font-size: 2rem;
      transition: color 0.3s ease, transform 0.3s ease;

      &:hover {
        color: #ffcc00; /* Màu vàng khi hover */
        transform: scale(1.3); /* Phóng to biểu tượng */
      }
    }
  }

  &__bottom {
    margin-top: 20px;
    font-size: 1rem;
    font-weight: 300;
    color: #ccc;
    transition: color 0.3s ease;

    &:hover {
      color: #ffcc00; /* Màu vàng khi hover */
    }
  }
}
