.parent {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 96px;
  padding: 20px;
  padding-top: 85px;
  max-height: 712px;
}

.div1 {
  grid-column: span 2 / span 2;
  position: relative;
}
.img_div1 {
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid orange;
  box-shadow: 0 0 3px 3px orange;
  display: flex;
  margin: auto;
  z-index: 3;
  position: relative;
  &:hover {
    box-shadow: 0 0 8px 8px orange;
    cursor: pointer;
  }
  &:hover + .img_div1-animation {
    display: block;
  }
  transition: all 1.5s linear;
  animation: imgDiv1 5.5s linear infinite;
}
@keyframes imgDiv1 {
  0% {
    border-radius: 50%;
  }
  100% {
    border-radius: 10% 50% 10% 50% / 50% 10% 50% 10%;
  }
}
.img_div1-animation {
  position: absolute;
  z-index: 2;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  filter: drop-shadow(5px 5px 5px greenyellow);
  display: none;
}

.div2 {
  grid-column: span 3; /* Phần tử sẽ chiếm 3 cột */
  display: grid;
  grid-template-columns: repeat(3, 1fr); // lưới có 3 cột
  gap: 40px;
  overflow: hidden;
  overflow-y: scroll;
  height: 626px;
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-track {
    background: white;
  }

  &::-webkit-scrollbar-thumb {
    background-image: linear-gradient(to right top, red, blue, yellow, green, pink);
    border-radius: 10px;
    cursor: pointer;
  }
}

.img_small {
  border-radius: 50%;
  width: 90px;
  height: 90px;
  box-shadow: 0 0 4px 4px greenyellow;
  margin: 0 auto;
  display: flex;
  object-fit: cover;
}

.content_item-small {
  min-height: auto;
  min-width: 205px;
  max-width: 205px;
  border-radius: 10px;
  background-color: blue;
  &:hover {
    box-shadow: 0 0 8px 8px green;
    transform: scale(1.1);
    cursor: pointer;
  }
  transition: all 200ms linear;
}

.content_item-text-small {
  font-size: 25px;
  color: red;
  text-shadow: 0 0 4px green;
  text-align: center;
  font-weight: 700;
  margin-top: 15px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  white-space: normal;
}

.info_item {
  padding: 10px;
  max-width: 170px;
  margin: 0 auto;
}
.content_item-text-small-des {
  font-size: 14px;
  color: black;
  margin-top: 5px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
}
.content_item-text-big {
  text-align: center;
  color: transparent;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 25px 19px;
  line-height: 1.2;
  text-shadow: 0 0 3px blue;
  font-weight: 700;
}
.content_item-text-des-big {
  text-align: center;
  font-size: 1.3rem;
  color: peru;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 5px 19px;
}
.info_item-big {
  max-width: 458px;
}
