.nav_header {
  position: fixed;
  min-width: 1347px;
  background-color: aliceblue;
  z-index: 10;
}

.nav_ul-item {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border-bottom: 2px solid springgreen;
}
.link_item {
  position: relative;
  min-width: 173px;
  min-height: 72px;
  text-align: center;
  &:hover {
    color: yellowgreen;
    text-shadow: 0 0 4px green;
    transform: rotateZ(360deg);
  }
  &::before {
    content: '';
    position: absolute;
    top: 5px;
    width: 10px;
    height: 10px;
    background-color: purple;
    box-shadow: 0 0 2px 2px purple, 0 0 3px 3px purple, 0 0 4px 4px purple, 0 0 5px 5px purple;
    border-radius: 50%;
    animation: circle_animation 3s linear infinite;
  }
  transition: all 2.2s linear;
  animation: link_item-animation 3.2s linear infinite;
}
@keyframes circle_animation {
  0% {
    top: 5px;
    left: 0;
  }
  10% {
    top: 5px;
    left: 100%;
  }
  20% {
    top: 10px;
    left: 50px;
  }
  30% {
    top: 30px;
    left: 0;
    box-shadow: 0 0 2px 2px goldenrod, 0 0 3px 3px goldenrod, 0 0 4px 4px goldenrod, 0 0 5px 5px goldenrod;
  }
}
@keyframes link_item-animation {
  0% {
    min-width: 172px;
    text-align: center;
  }
  50% {
    color: teal;
    text-shadow: 0 0 4px teal;
    font-style: italic;
    font-weight: 700;
  }
  30% {
    color: yellow;
    text-shadow: 0 0 4px yellow;
    font-style: italic;
    font-weight: 700;
  }
}
.item {
  display: flex;
  align-items: center;
  height: 100%;
}
.item_text {
  margin-left: 8px;
}
